export default class App extends Vue {

  get locale() {
    return this.$store.state.locale;
  }
  
  private  isMobile() {
   if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
     return true
   } else {
     return false
   }
 }

